.Card{
    width: 10rem;
    height: 15rem;
    position:absolute;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    gap:0.1rem;
    background: rgba(255, 255, 255, 0.26);
    border: 4px solid var(--orange);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0rem 6px 5rem 6px;

}

.Card>img{
    transform: scale(.4);
    margin-bottom: -2rem;
    margin-top: -2rem;
} 
.card span:nth-child(2){
    color: var(--gary);
    font-size: 20px;
    font-weight: bold;
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;

}
.card span:nth-child(3){
    font-size: 14px;
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.Card .c-button{
    background-color: #fff;
    box-shadow: 0px 19px 60px rgba(0, 0, 0, 0.08);
    border: none;
    padding: 3px;
    font-size: 16px;
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
    color: #5290FD;
}

.cards-right{
    position: relative;
    margin-left: 5rem;
}

.cards-right>*{
    position: absolute;
}
@media screen and (max-width:480px) {
    .Card{
        width: 15rem;
        height: 16rem;
        gap:.5rem;
    }
    
    .Card>img{
        transform: scale(.8);
        margin-bottom: -2rem;
    } 
    .card span:nth-child(2){
        color: var(--gary);
        font-size: 20px;
        font-weight: bold;
        font-family: 'Gudea', sans-serif;
        font-family: 'Nunito', sans-serif;
    
    }
    .card span:nth-child(3){
        font-size: 12px;
        font-family: 'Gudea', sans-serif;
        font-family: 'Nunito', sans-serif;
    }
    .Card .c-button{
        background-color: #fff;
        box-shadow: 0px 19px 60px rgba(0, 0, 0, 0.08);
        border: none;
        padding: 15px;
        font-size: 16px;
        font-family: 'Gudea', sans-serif;
        font-family: 'Nunito', sans-serif;
        color: #5290FD;
    }
    
    .cards-right{
        display: flex;
        flex-direction: column;
        gap: 17rem;
        margin-right: 20rem;
    }
    
    .cards-right>*{
       position: static;
       transform: scale(0.7);
    }
}