@import url('https://fonts.googleapis.com/css2?family=Gudea&family=Nunito:wght@300&display=swap');

.intros {
  display: flex;
  height: 65vh;
  margin-bottom: 6rem;
  margin-top: 6rem;
  margin-left: 4rem;
  margin-right: 4rem;
  justify-content: space-around;
  /* align-items: center; */
  
}
.in-left {
  display: flex;
  position: relative;
  flex: 1;
  flex-direction: column;
  gap: 2rem;
}
.in-left .in-name {
  flex-direction: column;
  display: flex;
}
.in-left .in-name > :nth-child(1) {
  color: var(--black);
  font-weight: bold;
  font-size: 3rem;
  font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.in-left .in-name > :nth-child(2) {
  font-weight: bold;
  font-size: 3rem;
  color: var(--orange);
  font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.in-left .in-name > :nth-child(3) {
  color: var(--gary);
  font-size: 14px;
  font-weight: bold;
  margin-top: 10px;
  font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.i-button {
  width: 8rem;
  height: 3rem;
}
.in-socal-icon {
  margin-top: 3rem;
  display: flex;
  gap: 1rem;
}
.in-socal-icon img {
  height: 3rem;
}
.in-socal-icon img:hover {
  cursor: pointer;
}

.in-right {
  flex: 1;
  position: relative;
}
.in-right img {
  position: absolute;
  z-index: 1;
}

.in-right > :nth-child(1) {
  transform: scale(0.69);
  left: -13%;
  top: -9%;
}
.in-right > :nth-child(2) {
  top: -5.6rem;
  transform: scale(0.6);
  left: -3rem;
}
.in-right > :nth-child(3) {
  transform: scale(1);
  left: 6rem;
  top: -1rem;
}
.in-right > :nth-child(4) {
  transform: scale(0.8);
  left: -7rem;
  top: -4rem;
  border-radius: 50%;
  padding: 0;
}
.blur {
  position: absolute;
  width: 22rem;
  height: 14rem;
  border-radius: 50%;
  background: #edd0ff;
  z-index: -9;
  top: 18%;
  left: 46%;
  filter: blur(72px);
}

/* smart screen */
@media screen and (max-width:480px) {
  .intros {
    flex-direction: column;
    margin-top: 1rem;
    margin-left: 0rem; 
  }
  .in-left {
    gap: 0.5rem;
  }
  .in-left .in-name {
    flex-direction: column;
    display: flex;
  }
  .in-left .in-name > :nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 2rem;
    font-family: 'Gudea', sans-serif;
      font-family: 'Nunito', sans-serif;
  }
  .in-left .in-name > :nth-child(2) {
    font-weight: bold;
    font-size: 2rem;
    color: var(--orange);
    font-family: 'Gudea', sans-serif;
      font-family: 'Nunito', sans-serif;
  }
  .in-left .in-name > :nth-child(3) {
    color: var(--gary);
    font-size: 12px;
    font-weight: bold;
    margin-top: 10px;
    font-family: 'Gudea', sans-serif;
      font-family: 'Nunito', sans-serif;
  }
  .i-button {
    margin-top: 10px;
    width: 7rem;
    height: 3rem;
  }
  .in-socal-icon {
    margin-top: 1rem;
    display: flex;
    gap: 0.5rem;
  }
  .in-socal-icon img {
    height: 2rem;
  }
  .in-right {
transform: scale(0.5);
margin-left: -4rem;
margin-top: 2rem;
  }
  .in-right > :nth-child(1) {
    transform: scale(0.69);
    left: -13%;
    top: -9%;
  }
  .in-right > :nth-child(2) {
    top: -5.6rem;
    transform: scale(0.6);
    left: -3rem;
  }
  .in-right > :nth-child(3) {
    transform: scale(1);
    left: 6rem;
    top: -1rem;
  }
  .in-right > :nth-child(4) {
    transform: scale(0.8);
    left: -7rem;
    top: -4rem;
    border-radius: 50%;
    padding: 0;
  }
  .blur {
display: none;
  }
}