.n-wrapper {
  height: 10vh;
  display: flex;
  /* justify-content: space-between; */
  margin: 0 4rem;
  position:sticky;
  top: 10px;
}

.n-left {
  flex: 1;
  align-items: center;
  display: flex;
  gap: 2rem;
}
.n-left .n-logo {
  font-size: 1.3rem;
  font-weight: bold;
}
.n-right {
  display: flex;
  flex: -1;
  align-items: center;
  justify-content: center;
  font-weight: 400;
}
.n-right .n-list {
  flex: 10;
}
.n-right .n-list ul {
  display: flex;
  list-style: none;
  gap: 2rem;
  margin-right: 4rem;
}
.n-right .n-list ul li {
    text-decoration: none;
    color: var(--black);
    font-weight: 600;
}
.n-right .n-list ul li:hover {
  cursor: pointer;
  color:var(--orange);
}
@media screen and (max-width:480px) {
  .n-wrapper {
    height: 10vh;
    display: flex;
    /* justify-content: space-between; */
    margin: 0 1rem;
    position:sticky;
    top: 10px;
  }
.n-right{
  display: none;
}
.n-left {

  gap: 1rem;
  margin-left: -1rem;
}
.n-left .n-logo {
  font-size:20px;
  font-weight: bold;
}
.n-button{
  display: none;
}
}