.testimonials{
    padding: 0 3rem 0 3rem;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 7rem;
    flex-direction: column;
    gap: 2rem;
    /* position: relative; */
}
.t-heading{
    align-self:center;
    
}
.t-heading>*{
    font-size: 2rem;
    font-weight: bold;
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.t-heading span:nth-of-type(2){
    color: var(--orange);
}
.blur-t{
   
    left: 38rem;
    top: 16rem;
  
}
.wrapp-t{
    align-self:center;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-evenly;
    padding: 1rem;
    /* width: 30rem; */
    height: 12rem;
    background-color: rgba(255, 255, 255, 0.26);
    /* border: 7px solid var(--blueCard); */
    /* box-shadow: var(--boxShadow); */
    border-radius: 20px;
    /* position: absolute; */
    /* margin-left: 19rem; */
}
.wrapp-t>img{
    border-radius: 50%;
}
.wrapp-t>span{
    color: var(--gray);
    width: 40rem;
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}

@media screen and (max-width:480px) {
    .testimonials{
        padding: 0 3rem 0 3rem;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 7rem;
        flex-direction: column;
        gap: 1rem;
        /* position: relative; */
    }
    .t-heading{
        align-self:center;
        text-align: center;
        
    }
    .t-heading>*{
        text-align: center;
        font-size: 1rem;
        font-weight: bold;
        font-family: 'Gudea', sans-serif;
        font-family: 'Nunito', sans-serif;
        gap: 3rem;
    }
    .t-heading span:nth-of-type(2){
        color: var(--orange);
    }
    .wrapp-t{
        align-self:center;
    
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content:space-evenly;
        padding: -1rem;
        /* width: 30rem; */
        height: 12rem;
        background-color: rgba(255, 255, 255, 0.26);
        /* border: 7px solid var(--blueCard); */
        /* box-shadow: var(--boxShadow); */
        border-radius: 20px;
        /* position: absolute; */
        /* margin-left: 19rem; */
    }
    .wrapp-t>img{
        border-radius: 50%;
    }
    .wrapp-t>span{
     display: none;
        
    }
}