.work {
  padding: 0 3rem 0 3rem;
  display: flex;
  height: 90vh;
  justify-content:space-between;
  /* align-items: center; */
  margin-top: 4rem;
}
.work-right {
  margin-right: 10rem;
  position: relative;
}
.work-right .main-circle {
  position: relative;
  left: 9rem;
  width: 18rem;
  height: 18rem;
  border-radius: 100%;
  box-shadow: var(--smboxShadow);
  top: 2rem;
  background-color: white;
}
.main-circle > * {
  position: absolute;
}

.main-circle > :nth-child(1) {
  left: -3rem;
  top: 6rem;
}
.main-circle > :nth-child(2) {
  left: 6rem;
  top: -4rem;
}
.main-circle > :nth-child(3) {
  left: 6rem;
  top: 5rem;
}
.main-circle > :nth-child(4) {
  left: 15rem;
  top: 5rem;
}
.main-circle > :nth-child(5) {
  left: 6rem;
  top: 13rem;
}

.sec-circle{
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #cbebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: var(--smboxShadow);
    background: white;
}

.sec-circle >img{
    transform: scale(0.4);
}

.b-circle{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}
.b-blue{
    background-color: #1949B8;
    left: 18rem;
    top: 0rem;
}
.b-yellow{
    background-color: #F5C32C;
    left: 18rem;
    top: 8rem;
}

@media screen and (max-width:480px) {
  .work {
    padding: 0 1rem 0 1rem;
    flex-direction: column;
  }
  .work-left{
    margin-top: 3rem;
    gap: 0rem;
  }
  .work-left>:nth-child(1){
    color: var(--black);
    font-size: 2rem;
    font-weight: bold;
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.work-left>:nth-child(2){
    font-size: 2rem;
    color: var(--orange);
    font-weight:bold; 
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.work-left>:nth-child(3){
    font-size: 12px;
    color: var(--gary);
    /* width:25rem; */
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.work-right{
  top: -5rem;
  transform: scale(0.7);
  left: -5rem;

}
.s-button{
  width: 10rem;
  height: 3rem;
}
}