.floatingdiv{
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    box-shadow: var(--boxShadow);
    align-items: center;
    height: 4rem;
    width: 15rem;
    border-radius: 17px;
    padding:5px 10px;
}

.floatingdiv>img{
    transform: scale(0.4);
    margin-left: -1rem;
}
.floatingdiv>p{
    position: absolute;
    left: 6rem;
    font-weight:bolder;
    font-size: 17px;
}
