.toggle{
    display: flex;
    justify-content: space-between;
    border: 3px solid var(--orange);
    border-radius: 1rem;
    padding: 2px;
    position: relative;
    cursor: pointer;
}

.toggle>*{
    color: var(--orange);
    width: 1rem;
    height: 1rem;
   
}

.toggle-button{
    border-radius: 100%;
    background-color: var(--orange);
    position: absolute;
    border: none;
}