@import url('https://fonts.googleapis.com/css2?family=Gudea&family=Nunito:wght@300&display=swap');

.service{
    margin: 0 4rem;
    padding: 0 0.3rem;
    display: flex;
    height: 90vh;
    margin-top: 9rem;
    margin-bottom: 13rem;
    /* justify-content: space-around; */
}
.awsome-left{
    display: flex;
    flex-direction: column;
    position: relative;
    gap: 0.5rem;
}
.awsome-left>:nth-child(1){
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.awsome-left>:nth-child(2){
    font-size: 2.5rem;
    color: var(--orange);
    font-weight:bold; 
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.awsome-left>:nth-child(3){
    font-size: 14px;
    color: var(--gary);
    /* width:25rem; */
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.s-button{
    width: 10rem;
    height: 3rem;
    margin-top: 1rem ;
}

.s-blur{
    top: 13rem;
    left: -18rem;
}


/* smart screen */
@media screen and (max-width:480px) {
    .service{
        flex-direction: column;
        margin: 0 0.5rem;
        padding: 0;
        height: 90vh;
        margin-top: 9rem;
        margin-bottom: 13rem;
        /* justify-content: space-around; */
    }
    .awsome-left{
        margin-top: 3rem;
        gap: 0rem;
    }
    .awsome-left>:nth-child(1){
        color: var(--black);
        font-size: 2rem;
        font-weight: bold;
        font-family: 'Gudea', sans-serif;
        font-family: 'Nunito', sans-serif;
    }
    .awsome-left>:nth-child(2){
        font-size: 2rem;
        color: var(--orange);
        font-weight:bold; 
        font-family: 'Gudea', sans-serif;
        font-family: 'Nunito', sans-serif;
    }
    .awsome-left>:nth-child(3){
        font-size: 12px;
        color: var(--gary);
        /* width:25rem; */
        font-family: 'Gudea', sans-serif;
        font-family: 'Nunito', sans-serif;
    }
    .s-button{
        width: 8rem;
        height: 2rem;
        margin-top: 1rem ;
    }
}