:root {
  --yellow: #f5c32c;
  --orange: #f79e10;
  --black: #000;
  --gray: #788097;
  --blueCard: #ddf8fe;
  --purple: rgb(238, 210, 255);
  --boxShadow: 0px 19px 60px rgba(0 0 0 / 10%);
  --orangeCard: rgba(252, 166, 31, 0.45);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
  --white: #fff;
  --background: linear-gradient( #fdc50f 26.71%, #fb982f 99.36%);
}
.App {
  padding: 0.5rem 3.5rem;
  overflow: hidden;
  color: var(--black);
}

.button {
  border-radius: 34px;
  border: none;
  color: var(--white);
  font-size: 16px;
  padding: 11px 26px;
background:var(--orange);
box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
  font-weight: 600;
}
.button:hover {
  border: 2px solid var(--orange);
  background-color: var(--white);
  cursor: pointer;
  color: var(--orange);
}

/* smart screen */
@media screen and (max-width:480px) {
  
  .App{
    padding: 0.5rem 1rem;
  }
}