.experience {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5rem;
  height: 30vh;
  margin-bottom: 8rem;
}
.achievement {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.achievement span:nth-of-type(2){
    color: var(--orange);

}

.circle {
  width: 4rem;
  height: 4rem;
  background-color: #fff;
  border-radius: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
  font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.circle::before {
    border-radius: 100%;
    content:'';
    background: linear-gradient(to bottom, #87e6fb 0%, #ffc05c 100%);
    top: -8px;
    left: -8px;
    right: -8px;
    bottom: -8px;
    position: absolute;
    z-index: -1;
    box-shadow: var(--boxShadow);
  
  }
  @media screen and (max-width:480px) {
    .experience {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 2rem;
      height: 100vh;
      margin-bottom: -5rem;
    }
