.portfolio{
    /* display: flex; */
    align-items: center;
    justify-content: center;
    text-align: center;
    /* flex-direction: column; */
    height: 70vh;
}

.portfolio span:nth-of-type(1){
    color: var(--black);
    font-size: 2rem;
    font-weight: bold;
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.portfolio span:nth-of-type(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
/* slider */
.portfolio-slider{
    margin-top: 3rem;
   
}

.portfolio-slider>:nth-child(1){
margin-left: 2.5rem;
}

.portfolio .portfolio-slider .item img{
    width: 19rem;
    filter: drop-shadow(-12px 15px 13px rgba(0,0,0,0.25));
    border-radius: 19px;
}

@media screen and (max-width:480px) {
    .portfolio{
margin-top: 12rem;
        height: 90vh;
        display: none;
    }
}