.contact{
    width: 100%;
    height: 100vh;
    display: flex;
    gap: 6rem;
    /* align-items: center; */
    justify-content: space-around;
    
}
.con-left{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;

}
.con-left span:nth-child(1){
    font-size: 3rem;
    font-weight: bold;
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}
.con-left span:nth-child(2){
    font-size: 3rem;
    font-weight: bold;
    color: var(--orange);
    font-family: 'Gudea', sans-serif;
    font-family: 'Nunito', sans-serif;
}

.con-right{
    
}

.con-right form{
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.con-right form>*{
    width: 20rem;
    border: 2px solid var(--orange);
    outline: none;
    border-radius: 10px;
    padding:10px;
}

@media screen and (max-width:480px) {
    .contact{
        flex-direction: column;
        gap: 2rem;   
    }
    .con-left{
        display: flex;
        flex-direction: column;
        gap: 0rem;
        align-items: center;
    
    }
    .con-left span:nth-child(1){
        font-size: 2rem;

    }
    .con-left span:nth-child(2){
        font-size: 2rem;

    }
    .con-right form{

        gap: 1rem;
    }
    .con-right form>*{
        width:100%;

    }
}